<template>
	<div>
		<transition name="move">
			<div v-show="showFlag" class="food" ref="food">
				<div class="food-content">
					<div class="image-header">
						<img :src="food.image">
						<div class="back" @click="hide">
							<i class="icon-arrow_lift"></i>
						</div>		
					</div>
					<div class="content">
						<h1 class="title">{{food.name}}</h1>
						<div class="detail">
							<span class="sell-count">月售{{food.sellCount}}份</span>
							<span class="rating">好评率{{food.rating}}%</span>
						</div>
						<div class="price">
							<span class="now">{{food.price}}</span><span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
						</div>
						<div class="cartcontrol-wrapper">
							<Cartcontrol :food="food" @cart-add="_drop"></Cartcontrol>
						</div>
						<transition name="fade">
							<div @click.stop.prevent="addFirst" class="buy" v-show="!food.count || food.count===0">加入购物车</div>
						</transition>
					</div>
					<Split v-show="food.info"></Split>
					<div class="info" v-show="food.info">
						<h1 class="title">商品介绍</h1>
						<p class="text">{{food.info}}</p>
					</div>
					<Split ></Split>
					<div class="rating">
						<h1 class="title">商品评价</h1>
						<RatingSelect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings" @toggle="toggleContent" @select="select"></RatingSelect>
						<div class="rating-wrapper">
							<ul v-show="food.ratings && food.ratings.length">
								<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item border-1px">
									<div class="user">
										<span class="name">{{rating.username}}</span>
										<img :src="rating.avatar" class="avatar" width="12" height="12">
									</div>
									<div class="time">{{rating.rateTime | formatDate }}</div>
									<p class="text">
										<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
									</p>
								</li>
							</ul>
							<div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
						</div>
					</div>
				</div>	
			</div>
		</transition>
	</div>
</template>
<script>
	import BScroll from 'better-scroll'
	import Vue from 'vue'
	import {formatDate} from '../common/js/date'
	import Cartcontrol from './Cartcontrol.vue'
	import Split from './Split.vue'
	import RatingSelect from './RatingSelect.vue'

	//const Positive = 0; //正面评价
	//const Negative = 1; //负面评价
	const All = 2;      //全部评价
	
	export default{
		props:{
			food:{
				type: Object
			}
		},
		data(){
			return{
				showFlag: false,
				selectType: All,
				onlyContent: true,
				desc:{
					all: '全部',
					positive: '推荐',
					negative: '吐槽'
				}
			};
		},
		methods:{
			show(){
				this.showFlag = true;
				this.$nextTick(()=>{
					if(!this.scroll){
						this.scroll = new BScroll(this.$refs.food,{
							click: true
						});
					}else{
						this.scroll.refresh();
					}
				});
			},
			hide(){
				this.showFlag = false;
			},
			addFirst(event){
				if (!event._constructed) {
		          return;
		        }
		        this.$emit('cart-add', event.target);
		        Vue.set(this.food, 'count', 1);
			},
			_drop(target){
				this.$nextTick(()=>{
					this.$emit('cart-add',target);
				});
			},
			//监听事件子组件
			toggleContent(){
				this.onlyContent = !this.onlyContent;
				this.$nextTick(()=>{
					this.scroll.refresh();
				});
			},
			//监听事件子组件
			select(type){
				this.selectType = type;
				this.$nextTick(()=>{
					this.scroll.refresh();
				});
			},
			//绑定3个按钮类型和显示的内容
			needShow(type, text){
				//判断是否显示内容onlyContent=true并且没有文本，不展示
				if(this.onlyContent && !text){
					return false;
				}
				if(this.selectType === All){
					return true;
				}else{
					return type === this.selectType;
				}
			}
		},
		filters:{
			formatDate(time){
				let date = new Date(time);
				return formatDate(date,'yyyy-MM-dd hh:mm');
			}
		},
		components:{
			Cartcontrol,
			Split,
			RatingSelect
		}
	}
</script>
<style lang="less">
	@import "../assets/css/mixin.less";
	.food{
		position: fixed;
		top: 0;
		left: 0;
		bottom: 48px;
		z-index: 30;
		width: 100%;
		background: #fff;
		transform: translate3d(0,0,0);
		&.move-enter-active, &.move-leave-active{
			transition: all 0.2s linear;
		}
		&.move-enter, &.move-leave-active{
			transform: translate3d(100%,0,0);
		}
		.image-header{
			position: relative;
			width: 100%;
			height: 0;
			padding-top: 100%; /*相当于盒子的宽度计算的*/
			img{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
			.back{
				position: absolute;
				top: 10px;
				left: 0;
				.icon-arrow_lift{
					display: block;
					padding: 10px;
					font-size: 20px;
					color: #fff;
				}
			}
		}
		.content{
			position: relative;
			padding: 18px;
			.title{
				line-height: 14px;
				margin-bottom: 8px;
				font-size: 14px;
				font-weight: 700;
				color: rgb(7,17,27);
			}
			.detail{
				margin-bottom: 18px;
				line-height: 10px;
				height: 10px;
				font-size: 0;
				.sell-count, .rating{
					font-size: 10px;
					color: rgb(147,153,159);
				}
				.sell-count{
					margin-right: 12px;
				}
			}
			.price{
				font-weight: 700;
				line-height: 24px;
				.now{
					margin-right: 8px;
					font-size: 14px;
					color: rgb(240,20,20);
				}
				.old{
					text-decoration: line-through;
					font-size: 10px;
					color: rgb(147,153,159); 
				}
			}
			.cartcontrol-wrapper{
				position: absolute;
				right: 12px;
				bottom: 12px;
			}
			.buy{
				position: absolute;
				right: 18px;
				bottom: 18px;
				z-index: 10;
				height: 24px;
				line-height: 24px;
				padding: 0 12px;
				box-sizing: border-box;
				font-size: 10px;
				border-radius: 12px;
				color: #fff;
				background: rgb(0,160,220);
				opacity: 1;
				&.fade-enter-active, &.fade-leave-active{
					transition: all 0.2s;
				}
				&.fade-enter, &.fade-leave-active{
					opacity: 0;
				}
			}
		}
		.info{
			padding: 18px;
			.title{
				line-height: 14px;
				margin-bottom: 6px;
				font-size: 14px;
				color: rgb(7,17,27);
			}
			.text{
				font-size: 12px;
				font-weight: 200;
				line-height: 24px;
				padding: 0 8px;
				color: rgb(77,85,93);
			}
		}
		.rating{
			padding-top: 18px;
			.title{
				line-height: 14px;
				margin-left: 18px;
				font-size: 14px;
				color: rgb(77,85,93);
			}
			.rating-wrapper{
				padding: 0 18px;
				.rating-item{
					position: relative;
					padding: 16px 0;
					.border-1px(rgba(7,17,27,0.1));
					.user{
						position: absolute;
						right: 0;
						top: 16px;
						font-size: 0;
						line-height: 12px;
						.name{
							display: inline-block;
							vertical-align: top;
							font-size: 10px;
							color: rgb(147,153,159);
							margin-right: 6px;
						}
						.avatar{
							border-radius: 50%;
						}
					}
					.time{
						line-height: 12px;
						font-size: 10px;
						color: rgb(147,153,159);
						margin-bottom: 6px;
					}
					.text{
						font-size: 12px;
						line-height: 16px;
						color: rgb(7,17,27);
						.icon-thumb_up, .icon-thumb_down{
							line-height: 16px;
							margin-right: 4px;
							font-size: 12px;
						}
						.icon-thumb_up{
							color: rgb(0,160,220);
						}
						.icon-thumb_down{
							color: rgb(147,153,159);
						}
					}
				}
				.no-rating{
					padding: 16px 0;
					font-size: 12px;
					color: rgb(147,153,159);
				}
			}
		}
		
	}
</style>


