<template>
	<div class="seller" ref="seller">
		<div class="seller-content">
			<div class="overview">
				<h1 class="title">{{seller.name}}</h1>
				<div class="desc border-1px">
					<Star :size="36" :score="seller.score"></Star>
					<span class="text">({{seller.ratingCount}})</span>
					<span class="text">月售{{seller.sellCount}}单</span>
				</div>
				<ul class="remark">
					<li class="block">
						<h2>起送价</h2>
						<div class="content">
							<span class="stress">{{seller.minPrice}}</span>元
						</div>
					</li>
					<li class="block">
						<h2>商家配送</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryPrice}}</span>元
						</div>
					</li>
					<li class="block">
						<h2>平均配送时间</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryTime}}</span>分钟
						</div>
					</li>
				</ul>
				<div class="favorite" @click="toggleFavorite">
					<span class="icon-favorite" :class="{'active':favorite}"></span>
					<span class="text">{{favoriteText}}</span>
				</div>
			</div>
			<Split></Split>
			<div class="bulletin">
				<h1 class="title">公告与活动</h1>
				<div class="content-wrapper border-1px">
					<p class="content">{{seller.bulletin}}</p>
				</div>
				<ul v-if="seller.supports" class="supports">
					<li class="support-item border-1px" v-for="(item,index) in seller.supports">
						<span class="icon" :class="classMap[seller.supports[index].type]"></span>
						<span class="text">{{seller.supports[index].description}}</span>
					</li>
				</ul>
			</div>
			<Split></Split>
			<div class="pics">
				<h1 class="title">商家实景</h1>
				<div class="pic-wrapper" ref="picWrapper">
					<ul class="pic-list" ref="picList">
						<li class="pic-item" v-for="pic in seller.pics">
							<img :src="pic" width="120" height="90">
						</li>
					</ul>
				</div>
			</div>
			<Split></Split>
			<div class="info">
				<h1 class="title border-1px">商家信息</h1>
				<ul>
					<li class="info-item border-1px" v-for="info in seller.infos">{{info}}</li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script>
	import BScroll from 'better-scroll'
	import Star from './Star.vue'
	import Split from './Split.vue'
	import {saveToLocal,loadFromLocal} from '../common/js/store'
	export default{
		props:{
			seller:{
				type: Object
			}
		},
		data(){
			return{
				favorite: (()=>{
					return loadFromLocal(this.seller.id,'favorite',false);
				})()
			}
		},
		computed:{
			favoriteText(){
				return this.favorite ? '已收藏' : '收藏';
			}
		},
	    methods:{
	    	_initScroll() {
		        if (!this.scroll) {
		          this.scroll = new BScroll(this.$refs.seller, {
		            click: true
		          });
		        } else {
		          this.scroll.refresh();
		        }
		    },
	    	_initPics(){
	    		if(this.seller.pics){ //一开始没有数据，所以用watch监听
		    		let picWidth = 120;
		    		let margin = 6;
		    		let width = (picWidth+margin)*this.seller.pics.length-margin;
		    		this.$refs.picList.style.width = width +'px'; 	
		    		this.$nextTick(() => { //确保宽度有效
			        	if (!this.picScroll) {
					     	this.picScroll = new BScroll(this.$refs.picWrapper, {
				                scrollX: true,
				                eventPassthrough: 'vertical'
				            });
				    	}else{
				    		this.picScroll.refresh();
				    	}
		            });
		    	}
	    	},
	    	toggleFavorite(event){
	    		if(!event._constructed){
	    			return;
	    		}
	    		this.favorite = !this.favorite;
	    		saveToLocal(this.seller.id,'favorite',this.favorite);
	    	}
	    },
		created(){
	     	this.classMap = ['decrease','discount','special','invoice','guarantee'];      	  	
	    },
	    watch:{
	        'seller'() { //监听异步更新 有数据了再执行
          		this._initScroll();
            	this._initPics();
	        }
	    },
	    mounted(){
		    this._initScroll();
		    this._initPics();
	   	},
		components:{
			Star,
			Split
		}
	}
</script>
<style lang="less">
	@import "../assets/css/mixin.less";
	.seller{
		position: absolute;
        top: 174px;
        left: 0;
        bottom: 0;
        width: 100%;
        overflow: hidden;
        .overview{
        	padding: 18px;
        	position: relative;
        	.title{
        		font-size: 14px;
        		line-height: 14px;
        		color: rgb(7,17,27);
        		margin-bottom: 8px;
        	}
        	.desc{
        		padding-bottom: 18px;
        		font-size: 0;
        		.border-1px(rgba(7,17,27,0.1));
        		.star{
        			display: inline-block;
        			vertical-align: top;
        			margin-right: 8px;
        		}
        		.text{
        			display: inline-block;
        			vertical-align: top;
        			margin-right: 12px;
        			line-height: 18px;
        			font-size: 10px;
        			color: rgb(77,85,93);
        		}
        	}
        	.remark{
        		display: flex;
        		padding-top: 18px;
        		.block{
        			flex: 1;
        			text-align: center;
        			border-right: 1px solid rgba(7,17,27,0.1);
        			&:last-child{
        				border-right: none;
        			}
        			h2{
        				font-size: 10px;
        				line-height: 10px;
        				color: rgb(147,153,159);
        				margin-bottom: 4px;
        			}
        			.content{
        				font-size: 10px;
        				line-height: 24px;
        				color: rgb(7,17,27);
        				.stress{
        					font-size: 24px;
        				}
        			}
        		}
        	}
        	.favorite{
        		position: absolute;
        		right: 11px;
        		top: 18px;
        		width: 50px;
        		text-align: center;
        		.icon-favorite{
        			display: block;
        			font-size: 24px;
        			color: #d4d6d9;
        			line-height: 24px;
        			margin-bottom: 4px;
        			&.active{
        				color: rgb(240,20,20);
        			}
        		}
        		.text{
        			font-size: 10px;
        			line-height: 10px;
        			color: rgb(77,85,93);
        		}
        	}
        }
        .bulletin{
        	padding: 18px 18px 0;
        	.title{
        		font-size: 14px;
        		line-height: 14px;
        		color: rgb(7,17,27);
        		margin-bottom: 8px;
        	}
        	.content-wrapper{
        		padding: 0 12px 16px;
        		.border-1px(rgba(7,17,27,0.1));
        		.content{
        			line-height: 24px;
        			font-size: 12px;
        			color: rgb(240,20,20);
        		}
        	}
        	.supports{
        		.support-item{
        			padding: 16px 12px;
        			font-size: 0;
        			.border-1px(rgba(7,17,27,0.1));
        			&:last-child{
        				.border-none();
        			}
        			.icon{
						display: inline-block;
						vertical-align: top;
						width: 12px;
						height: 12px;
						margin-right: 4px;
						background-size: 12px 12px;
						background-repeat: no-repeat;
						&.decrease{
							.bg-image('decrease_4');
						}
						&.discount{
							.bg-image('discount_4');
						}
						&.guarantee{
							.bg-image('guarantee_4');
						}
						&.invoice{
							.bg-image('invoice_4');
						}
						&.special{
							.bg-image('special_4');
						}
					}
					.text{
						font-size: 12px;
						line-height: 16px;
						color: rgb(7,17,27);
					}
				
        		}
        	}
        }
        .pics{
        	padding: 18px;
        	.title{
        		font-size: 14px;
        		line-height: 14px;
        		color: rgb(7,17,27);
        		margin-bottom: 12px;
        	}
        	.pic-wrapper{
        		width: 100%;
        		overflow: hidden;
        		white-space: nowrap; /*图片超过屏幕宽度不折行*/
        		.pic-list{
        			font-size: 0;
        			.pic-item{
        				display: inline-block;
        				margin-right: 6px;
        				width: 120px;
        				height: 90px;
        				&:last-child{
        					margin-right: 0;
        				}
        			}
        		}
        	}
        }
        .info{
        	padding: 18px 18px 0; 
        	.title{
        		font-size: 14px;
        		line-height: 14px;
        		color: rgb(7,17,27);
        		padding-bottom: 12px;
        		.border-1px(rgba(7,17,27,0.1));
        	}
        	.info-item{
        		padding: 16px 12px;
        		font-size: 12px;
        		line-height: 16px;
        		.border-1px(rgba(7,17,27,0.1));
        		&:last-child{
        			border: none;
        		}
        	}
        }
	}
</style>


